.menuDetail {
    height: 100%;
    padding: 20px;
    border-radius: 6px;
    grid-gap: 20px;
    letter-spacing: 2px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 1.2rem;
    background-color: white;
    text-align: center;
    margin: 20px;
    margin-bottom: 100px;
    margin-top: 70px;
    flex-direction: column;
}

.menuDetailHr {
    width: 100%;
}

.menuDetail #S,
.menuDetail #B,
.menuDetail #F {
    width: 100%;
}

.reviewButton {
    margin: 0 auto;
    padding: 10px;
    border: none;
    border-radius: 10px;
    background-color: rgb(80, 176, 209);
    text-align: center;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    width: 100%;
    color: white;
}

.menuDetailImg {
    width: 100%;
    height: 100%;
    border-radius: 10%;
    margin-bottom: 10px;
    max-width: 300px;
    max-height: 300px;
    border-radius: 5%;
}

footer {
    position: fixed;
    background-color: #555;
    color: white;
    left: 0;
    bottom: 0;
    width: 98%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-direction: row;
    padding-right: 2%;
}

.inputCart {
    margin: 0 auto;
    padding: 10px;
    border: none;
    border-radius: 10px;
    background-color: white;
    text-align: center;
    display: flex;
    flex-direction: row;
    align-items: center;
    place-content: center;
    font-size: 1.2rem;
    width: 100%;
}

.sold-out-overlay img::before {
    content: 'Sold Out';
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: #000;
    opacity: 0.2
}

@media screen and (min-width: 1000px) {

    .menuDetailInfo {
        padding-left: 18px;
        text-align: left;
        display: flex;
        flex-direction: column;
    }

    .menuDetailName {
        font-size: 25px;
        margin-top: 7px;
        margin-bottom: 9px;
        font-weight: 600;
    }

    .menuDetailPrice {
        font-size: 20px;
    }
}

.modal {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    justify-content: center;
    align-items: center;
}

.modalContent {
    position: relative;
    background-color: #fefefe;
    padding: 20px;
    border: 1px solid #888;
    width: 300px;
    font-size: 20px;
    text-align: center;
    border-radius: 6px;
}

.modal img {
    width: 200px;
}

.close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
    cursor: pointer;
    position: absolute;
    top: 5px; /* 원하는 상단 여백 조정 */
    right: 10px; /* 원하는 오른쪽 여백 조정 */
}

.close:hover {
    color: black;
}


@media screen and (max-width: 1000px) {

    nav ul {
        list-style-type: none;
        margin: 0;
        padding: 0;
        width: 100%;
        background-color: #f1f1f1;
        position: fixed;
        height: 80px;
        overflow: auto;
        align-items: center;
    }

    .menuDetailImg {
        width: 100%;
        border-radius: 10%;
        margin-bottom: 10px;
        max-width: 250px;
        border-radius: 5%;
    }

    .menuDetail {
        height: 100%;
        padding: 20px;
        border-radius: 6px;
        grid-gap: 20px;
        letter-spacing: 2px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        font-size: 1.2rem;
        background-color: white;
        text-align: center;
        margin: 20px;
        margin-bottom: 100px;
        margin-top: 70px;
    }

    .menuDetailHr {
        display: block;
    }

    .menuDetailInfo {
        padding-left: 18px;
        text-align: left;
        display: flex;
        flex-direction: column;
    }

    .menuDetailName {
        font-size: 25px;
        margin-top: 7px;
        margin-bottom: 9px;
        font-weight: 600;
    }

    .menuDetailPrice {
        font-size: 20px;
    }
}