.body {
    overflow: hidden;
}

.logoDiv {
    background-color: white;
    border-bottom: 1px solid rgb(173, 173, 173);
    display: flex;
}

.logo {
    width: 12%;
    min-width: 140px;
    padding-left: 10%;
    margin: 20px 0px;
}

#loginDiv {
    font-size: 14px;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh; /* calc(100vh - 85px) */
    background-image: url('../img/UI_pattern.jpg');
    background-size: 1200px;
    background-repeat: no-repeat; 
    background-attachment: fixed; 
    background-position: right bottom; 
    background-color: #fff;
    width: 100%;
}

.loginWrapper{
    min-width: 350px;
    width: 35%;
    box-sizing: border-box;
    margin-top: 70px;
    background-color: white; 
    padding: 20px 25px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}


#loginH2 {
    font-size: 24px;
    color: rgb(80, 176, 209);
    margin-bottom: 20px;
}

#loginForm > input{
    width: 100%;
    height: 50px;
    padding: 0 10px;
    box-sizing: border-box;
    margin-bottom: 16px;
    font-size: 14px;
    border-radius: 6px;
    background-color: #ededed;
    border: 0;
}

#loginForm > input::placeholder{
    color: #D2D2D2;
}

#loginForm > input[type="submit"]{
    color: #fff;
    font-size: 16px;
    background-color: rgb(80, 176, 209);
    margin-top: 20px;
    cursor: pointer;
}


.remember-check {
    display: none;
  }
  
  .label {
    color: #999999;
  }
  
  .remember-check + label {
    cursor: pointer;
    padding-left: 26px;
    background-image: url("../img/checkbox.png");
    background-repeat: no-repeat;
    background-size: contain;
  }
  
  .remember-check:checked + label::after {
    background-image: url("../img/checkbox-active.png");
    background-repeat: no-repeat;
    background-size: contain;
  }
  


@media screen and (max-width: 700px) {


    .loginWrapper{
        width: 35%;
        box-sizing: border-box;
        margin-top: 130px;
    }

    #loginDiv {
        height: calc(100vh - 79px);
        background-image: url('../img/UI_pattern.jpg');
        background-size: 150%;
    }
    
}